import { initializeApp } from 'firebase/app';
import { getDatabase, ref, set } from "firebase/database";
import React, { useEffect, useState } from "react";
/**
 * Sanitize object keys recursively
 * Removes forbidden chars: . # $ / [ ]
 * Skips keys that become empty
 */
function sanitizeKeys(obj) {
    if (Array.isArray(obj)) {
      return obj.map(sanitizeKeys);
    } else if (typeof obj === 'object' && obj !== null) {
      const newObj = {};
      for (const key in obj) {
        if (Object.hasOwnProperty.call(obj, key)) {
          // Remove forbidden chars
          let newKey = key.replace(/[.#$/\[\]/]/g, '_');
          if (newKey.trim() === '') continue; // skip empty keys
  
          // Recurse
          newObj[newKey] = sanitizeKeys(obj[key]);
        }
      }
      return newObj;
    } else {
      return obj;
    }
  }

function writeUserData(dateStr, data) {
    if (process.env.NODE_ENV === 'production' && !window.location.href.includes('a=b')) {

        const db = getDatabase();
        set(ref(db, 'users/' + sanitizeKeys(dateStr)), sanitizeKeys(data));            
            }
  
}
