import { ArrowDownward, ArrowUpward } from '@mui/icons-material';
import { debounce, IconButton } from '@mui/material';
import React from 'react';
import  ProgressBar from './ProgressBar';
export const Scroll = () => {
    const toTop = () => {
        window.scrollTo({ top: 0, behavior: 'smooth' })

    }
    const toBottom = () => {
        window.scrollTo({ top: document.body.scrollHeight, behavior: 'smooth' })

    }
    const [isTopHalf, setIsTopHalf] = React.useState(true);

    React.useLayoutEffect(() => {
        const onScroll = debounce(() => {
            const total = document.body.scrollHeight;
            const scrollPosition = window.scrollY; // Current scroll position
            console.log({ scrollPosition, total }, scrollPosition < total/2, isTopHalf)
        
            if (scrollPosition < total/2) {
                console.log('if')
                setIsTopHalf(true)
            } else {
                setIsTopHalf(false);
            }
          }, 50);
        window.addEventListener('scroll', onScroll);
    },[])
  console.log({ isTopHalf })
    return <>
            <ProgressBar height="6" bgcolor="#9c27b0" duration="0.2" />

    <div
        style={{
            position: 'fixed',
            right: '20px',
            bottom: '20px'
        }}
    ><IconButton  color="secondary" aria-label="add an alarm">
    {isTopHalf ?<ArrowDownward onClick={toBottom} /> : <ArrowUpward onClick={toTop} /> 
    }
  </IconButton></div></>
}